import React, {useState , useEffect} from 'react'
import './comp.css'
import axios from 'axios'


export default function MainApp(props){

const [user , setUser] = useState("user")
const [leader , setLeader] = useState("Leader")
const [tasks , setTasks] = useState([])
const [padavans, setPadavans] = useState([])
const [usingpadavan, setUsingpadavan] = useState(user.username)
     axios({
     method: 'GET',
     url: 'http://127.0.0.1:8000/api/getname/'
     }).then((response) => {
        setUser(response.data);
        if (response.data ==="error"){
         window.location = "/"
        };
        }, [])

     useEffect( () => {axios({
     method: 'GET',
     url: 'http://127.0.0.1:8000/api/task/',
     data :{"Responser": {usingpadavan}}
     }).then((response) => {setTasks(response.data)})} , [])
     useEffect( () => {axios({
     method: 'GET',
     url: 'http://127.0.0.1:8000/api/padavan/'
     }).then((response) => {setPadavans(response.data)})} , [])

const { loggingIn, loggedIn } = props;
    return (
    <body>
      <header>
          <div className = "hello_btn" > Hello {user} </div>
          <div className = "nav_btn"> Your leader is: {leader} </div>
          <div className = " nav_btn , nav_btn_color"> Register </div>
          <div className = " nav_btn , nav_btn_color"> Logout </div>
      </header>
        <table className ="main_table">
            <td>
                <table className = "padavans">
                <tr><th>Список Падаванов </th></tr>
                <tr><td onClick={setPadavans(user.username)}>Туту напиши user.username</td></tr>
                {padavans.map (c => (
                <tr ><td onClick={setPadavans(c.username)}>{c.username} - {c.first_name} - {c.last_name}</td></tr>
                ))}
                 </table>
            </td>
            <td>
                <table className = "bone">
                <tr className="first_tr"><th>Заголовок</th><th>Дата создания</th><th>Срок</th><th>Приоритет</th><th>Статус</th> </tr>
                    {tasks.map(c => (
                        <tr><td>{c.Title}</td><td>{c.Date_update.substr(0,10)} {c.Date_update.substr(11,5)}</td><td>{c.Date_end.substr(0,10)} {c.Date_end.substr(11,5)}</td><td>{c.Priority}</td><td>{c.Status}</td> </tr>
                    ))}
                </table>

            </td>
        </table>
      </body>
    )
  }




